.blog img {
    width: 100%;
    height: 100%;
    border-radius: 5px;
    margin-bottom: 20px;
  }
  .blog .items {
    padding: 20px;
    background-color: #fff;
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
    border-radius: 5px;
    transition: 0.5s ease-in-out;
  }
  .category span {
    font-size: 14px;
    color: grey;
    transition: 0.5s ease-in-out;
  }
  .category label {
    background-color: #31b675;
    font-size: 13px;
    color: white;
    padding: 5px 15px;
    border-radius: 5px;
    transition: 0.5s ease-in-out;
  }
  .blog .items h3 {
    margin: 5px 0 10px 0;
    font-weight: 500;
    transition: 0.5s ease-in-out;
  }
  .blog .items p {
    font-size: 14px;
    color: grey;
    margin-bottom: 10px;
    transition: 0.5s ease-in-out;
  }
  .blog .items a {
    color: black;
    font-weight: 500;
    font-size: 14px;
    transition: 0.5s ease-in-out;
  }
  .blog .items a i {
    color: #31b675;
    transition: 0.5s ease-in-out;
  }
  .blog .items:hover {
    background-color: #31b675;
    cursor: pointer;
  }
  .blog .items:hover h3,
  .blog .items:hover span,
  .blog .items:hover a,
  .blog .items:hover a i,
  .blog .items:hover p {
    color: white;
  }
  .blog .items:hover label {
    background-color: #fff;
    color: #31b675;
    cursor: pointer;
  }
  @media screen and (max-width: 768px) {
    .blog .content.grid {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  @media screen and (max-width: 550px) {
    .blog .content.grid {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  /*---------blog single page------*/
  .category-list {
    color: black;
  }
  .category-list h1 {
    font-size: 30px;
  }
  .category-list hr {
    border: none;
    border-bottom: 3px dotted #31b675;
    width: 100px;
  }
  .category-list ul {
    margin-top: 20px;
  }
  .category-list ul li {
    margin-bottom: 15px;
    transition: 0.5s;
  }
  .category-list ul li:hover {
    color: #31b675;
    transform: translateX(10px);
    cursor: pointer;
  }
  .category-list i {
    color: #31b675;
    margin-right: 20px;
  }